<template>
	<div class="app">
		<form action="/">
			<van-search v-model="value" show-action placeholder="请输入搜索关键词" @search="onSearch" @input="onSearch"
				@cancel="goback" />
		</form>
		<div class="nav" v-show="list.length===0">
			<div class="nav-left" style="margin-left: 10px;">
				<p>搜索历史</p>
			</div>
			<div class="nav-right" @click="del">
				<div class="nav-text">
					<p>全部删除</p>
				</div>
				<van-icon name="delete-o" />
			</div>
		</div>
		<div class="nav-list" v-show="list.length===0" style="background-color: #fff;">
			<ul>
				<li v-for="item in search_history" :key="search_history.id" @click="todetails(item)"
					v-if="search_history.length>0">
					<p>{{item.title}}</p>
				</li>
			</ul>
		</div>
		<div class="list" v-show="list.length>0">
			<ul>
				<li v-for="item in list" :key='item.id'>
					<p @click="todetails(item)" style="flex: 9;text-align: left;">{{item.title}}</p>
					<van-icon name="close" style="flex: 1;" />
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		List,
		Toast,
	} from 'vant';
	export default {
		components: {
			[Toast.name]: Toast,
		},
		data() {
			return {
				name: 'searchs',
				value: '',
				list: [],
				records: '',
				search_history: []
			}
		},
		created() {
			let search_history = localStorage.getItem('search_history')
			if (search_history) {
				this.search_history = JSON.parse(search_history)
			}
		},
		methods: {
			onSearch(val) {
				if (val == '') {
					this.list = []
				}
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search', {
					key_word: val,
					skip: 0,
					limit: 10
				}).then(res => {
					console.log(res.data);
					this.list = res.data.data
					if (res.data.count === 0) {
						Toast('未搜索到相关内容')
					}
				})
			},
			todetails(item) {
				let index = this.search_history.findIndex(v => v.title === item.title);
				if (index >= 0) {
					this.search_history.splice(index, 1)
				}
				this.search_history.unshift({
					title: item.title,
					timestamp: +new Date(),
					_id: item._id
				})
				localStorage.setItem('search_history', JSON.stringify(this.search_history))
				this.$router.push({
					path: '/details',
					query: {
						article_id: item._id
					}
				})
			},
			del() {
				this.search_history = []
				localStorage.removeItem('search_history')
			},
			goback() {
				this.$router.back()
			}
		},

	}
</script>

<style lang="less" scoped>
	.app {
		height: 100vh;
		background-color: #f1f1f1;
	}

	.nav {
		padding-top: 10px;
		display: flex;
		justify-content: space-between;
		background-color: #fff;
	}

	.nav>p {
		padding-left: 15px;
	}

	.nav-right {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		margin-right: 15px;
	}

	.nav-text {
		display: flex;
		// width: 90%;
		text-align: right;
	}

	.van-icon {
		padding: 5px;
	}

	.list li {
		display: flex;
		justify-content: space-between;
		margin-top: 1px;
		background-color: #fff;
		height: 50px;
		align-items: center;
		padding-left: 15px;
		padding-right: 15px;
	}

	.nav-list>ul {
		text-align: left;
		margin-left: 10px;
	}

	.nav-list>ul>li {
		display: inline-block;
		font-size: 3.46667vmin;
		height: 9.06667vmin;
		line-height: 8.8vmin;
		padding: 0 3.2vmin;
		color: #505050;
		border: 1px solid #ccc;
		border-radius: 4.53333vmin;
		margin-right: 2.66667vmin;
		margin-top: 3.2vmin;
		margin-bottom: 10px;
	}
</style>
