<template>
	<div>
		<!-- 头部 -->
		<div>
			<van-nav-bar title="文章详情" left-arrow @click-left="onClickLeft" />
		</div>
		<!-- 内容 -->
		<div class="content">
			<!-- 标题 -->
			<h3>{{title}}</h3>
			<div class="user">
				<div class="userinfo" style="display: flex;">
					<!-- 头像 -->
					<div class="img" style="width: 50px;height: 50px;">
						<img :src="avatar" alt="" srcset="" style="width: 100%;height: 100%;border-radius: 50%;">
					</div>
					<!-- 名称 时间 -->
					<div class="info">
						<p>{{author}}</p>
						<span>{{new Date(1666365377288).toLocaleDateString()}}</span>
					</div>
				</div>
				<!-- 右侧关注按钮 -->
				<div class="infobtn" style="margin-right: 20px;">
					<button class="guanzhu" v-show="ishow" @click="changeshow">+关注</button>
					<button class="guanzhu" v-show="!ishow" @click="changeshow">已关注</button>
				</div>
			</div>
		</div>
		<!-- 内容和图片 -->
		<div class="list" style="border-bottom: 1px solid #eee;">
			<p style="text-align: left;margin-left: 10px;margin-bottom: 10px;word-wrap: break-word;">{{content}}</p>
			<div class="img" style="width: 95%;margin: 0 auto;margin-bottom: 10px;">
				<img :src="item" alt="内容图片" srcset="" style="width: 100%;height: 100%;" v-for="item in imageSrc">
			</div>
		</div>
		<!-- 评论列表 -->
		<comment :comment='comment' :fav='fav' :like='like' :uid='uid'></comment>
		<!-- 底部导航 -->
		<div class="footer">
			<!-- 评论 -->
			<div class="input">
				<van-icon name="edit" style="position: absolute;top: 2.13333vw;left: 2.13333vw;" />
				<input type="text" placeholder="写评论..." @keyup.enter="add" v-model="value">
			</div>
			<div class="icon" style="position: relative;">
				<van-icon name="chat-o" class="icons" @click="add" />
				<!-- 收藏 -->
				<van-icon name="star" class="icons" v-if="is_fav" color="#ee0a24" @click="cancelfav" />
				<van-icon name="star" class="icons" @click="likefav" v-else />
				<!-- 点赞 -->
				<van-icon name="good-job" class="icons" v-if="is_like" color="#ee0a24" @click="cancellikeit" />
				<van-icon name="good-job" class="icons" @click="likeit" v-else />
				<van-icon name="share-o" class="icons" />
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import comment from '@/views/article/comment.vue'
	import {
		Toast
	} from 'vant'
	export default {
		data() {
			return {
				name: 'details',
				content: '',
				title: '',
				imageSrc: '',
				time: '',
				author: '',
				like: '',
				comment: '',
				fav: '',
				avatar: '',
				ishow: true,
				uid: '',
				is_fav: false,
				is_like: false,
				value: '',
				commentlist: ''
			}
		},
		components: {
			comment
		},
		methods: {
			onClickLeft() {
				this.$router.back()
			},
			changeshow() {
				this.ishow = !this.ishow
			},
			// 收藏
			likefav() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_fav = true;
					}
				})
			},
			// 取消收藏
			cancelfav() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_fav = false;
					}
				})
			},
			// 点赞
			likeit() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_like = true;
					}
				})
			},
			// 取消点赞
			cancellikeit() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_like = false;
					}
				})
			},
			// 评论文章
			add() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
					content: this.value,
					comment_type: 0
				}).then(res => {
					console.log(res.data);
					if (res.data.code === 0) {
						this.$bus.$emit('getcomment', this.commentlist)
						Toast(res.data.msg)
						this.value = ''
					}
				})
			},
		},
		created() {
			// 获取文章详情
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			this.uid = userInfo.uid
			axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail', {
				article_id: this.$route.query.article_id,
			}).then(res => {
				// console.log(res.data);
				this.content = res.data.data.content
				this.title = res.data.data.title
				this.imageSrc = res.data.data.imageSrc
				this.time = res.data.data.time
				this.author = res.data.data.author
				this.like = res.data.data.like
				this.comment = res.data.data.comment
				this.fav = res.data.data.fav
				this.avatar = res.data.data.avatar
				this.is_fav = res.data.data.is_fav
				this.is_like = res.data.data.is_like
			}).catch(err => {
				console.log(err);
			})

		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/.van-nav-bar .van-icon {
		color: #fff;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	.van-nav-bar {
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
	}

	.content {
		margin-bottom: 20px;
		margin-top: 60px;
	}

	.content>h3 {
		text-align: left;
		margin-left: 20px;
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		margin-left: 10px;
	}

	.user {
		display: flex;
		margin-left: 20px;
		justify-content: space-between;
		align-items: center;
	}

	.footer {
		display: flex;
		padding: 2.66667vw 4.26667vw;
		position: fixed;
		bottom: -1px;
		left: 0;
		background-color: #fff;
		border-top: 1px solid #f4f4f4;
	}

	.footer .input {
		position: relative;
		flex: 4;
		display: flex;
	}

	.input>input {
		width: 100%;
		background-color: #f3f3f3;
		padding: 2.13333vw 0 2.13333vw 9.33333vw;
		border-radius: 4vw;
		outline: none;
		border: none;
	}

	.footer .icon {
		text-align: center;
		height: 9.33333vw;
		line-height: 11.33333vw;
	}

	.footer .icon .icons {
		margin: 0 4.26667vw 2.66667vw 2.66667vw;
		font-size: 6.4vw;
	}

	.guanzhu {
		border: 1px solid #ccc;
		height: 8vw;
		line-height: 8vw;
		width: 13.33333vw;
		text-align: center;
		font-size: 3.73333vw;
		margin-top: 1px;
		background-color: #fff;
		color: #000;
	}

	.likeit {}
</style>
